<script>
    let innerWidth = 0
    let innerHeight = 0

    let x = 1
    let y = 1

    function randomInteger(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }


    async function startCircleAnimation() {
        while (true) {
            y = randomInteger(-(innerHeight / 2) + 259 / 2, (innerHeight / 2) - 259 / 2)
            x = randomInteger(-(innerWidth / 2) + 276 / 2, (innerWidth / 2) - 276 / 2)

            await new Promise(r => setTimeout(r, 1200));
        }
    }

</script>

<style>
    .star {
        transition: all .5s ease-in-out;
        position: absolute;
    }
</style>

<svelte:window bind:innerWidth={innerWidth} bind:innerHeight={innerHeight}/>

<div class="star" on:click={startCircleAnimation()} style="transform: translateY({y}px) translateX({x}px)">
