<script>
    import Star from './Star.svelte'

    let colors = ["#3700F0", "#F04705", "#0BA210", "#FCF10B", "#EC028F", "#C4C4C4"]
    let texts = ["покой", "природа", "любовь", "дом", "эмоция", "семья", "земля", "радость", "сейчас", "страх", "эгоизм", "свобода"]

    function range(start, stop, step) {
        if (stop == null) {
            stop = start;
            start = 0;
        }
        if (!step) {
            step = 1;
        }
        const len = (stop - start) / step;
        return new Proxy({length: len}, {
            get: (t, prop) => {
                return prop === 'length' ? t.length :
                    prop < t.length ? start + (prop * step) : undefined;
            }
        });
    }

    function getRandomColor() {
        return colors[Math.floor(Math.random() * colors.length)];
    }

    function setRandomColor() {
        color = getRandomColor()
    }

    function getRandomText() {
        return texts[Math.floor(Math.random() * texts.length)];
    }

    function setRandomText() {
        text = getRandomText()
    }

    function tapBigCircle() {
        setRandomColor()
        setRandomText()
    }

    let color = getRandomColor()
    let text = getRandomText()

</script>


<main>

    <div class="big-circle" on:click={tapBigCircle} style="background-color: {color};">
        <p>{text}</p>
    </div>

    {#each range(6) as v,i}
        <Star/>
    {/each}

</main>

